<template>
	<view class="wPopup">
		<u-popup :show="value" mode="bottom" :round="10" @close="close">
			<view class="modalStyle">
				<view class="top">
					<view class="title">{{name}}</view>
					<view class="close">
						<image @click="close"
						  src="data:image/png;base64,"
						  mode=""></image>
					</view>
				</view>
				<view class="content">
					<slot></slot>
				</view>
				<view class="bottom" v-if='showBottomButton'>
					<w-button v-if='!doubleButton' class="center" @click='saveData' :name="oneButtonName">
					</w-button>
					<w-button v-else double @clickLeft='deteleData' @clickRight='editData' leftColor="#fff" leftBgColor="#f56c6c" :leftName="leftButtonName"
					  :rightName="rightButtonName"></w-button>
				</view>
			</view>
		</u-popup>
	</view>
</template>
<script>
	export default {
		name: "wPopup",
		props: {
			//v-model绑定的用于显示和隐藏的值
			value: {
				type: Boolean,
				default: false
			},
			//是否显示底部按钮
			showBottomButton: {
				type: Boolean,
				default: true
			},
			//是否显示两个按钮
			doubleButton: {
				type: Boolean,
				default: false
			},
			//弹框的头部标题名称
			name: {
				type: String,
				default: '名称'
			},
			//一个按钮的名称
			oneButtonName: {
				type: String,
				default: '保存'
			},
			//两个按钮时左边按钮的名称
			leftButtonName: {
				type: String,
				default: '删除'
			},
			//两个按钮时右边按钮的名称
			rightButtonName: {
				type: String,
				default: '修改'
			},
			//是否需要提示消息
			toast: {
				type: Boolean,
				default: true
			},
			//是否开启验证，开启后按完按钮不关闭popup
			validate: {
				type: Boolean,
				default: false
			}
		},
		data() {
			return {};
		},
		methods: {
			close() {
				this.$emit('input', false)
			},
			saveData() {
				this.$emit('save')
				if (this.toast) {
					this.$message.toast('保存成功')
				}
				if (this.validate) {} else {
					this.close()
				}
			},
			editData() {
				this.$emit('edit')
				if (this.toast) {
					this.$message.toast('修改成功')
				}
				if (this.validate) {} else {
					this.close()
				}
			},
			deteleData() {
				this.$emit('detele')
				if (this.toast) {
					this.$message.toast('删除成功')
				}
				this.close()
			}
		},
	}
</script>
<style lang="scss" scoped>
	.wPopup {
		.modalStyle {
			padding-top: 30rpx;

			.top {
				display: flex;
				justify-content: space-between;
				padding: 10rpx 30rpx;
				border-bottom: 2rpx solid #EEE;

				.title {
					font-size: 30rpx;
					font-weight: 700;
					color: #666666;
				}

				.close {
					width: 50rpx;
					height: 50rpx;

					image {
						width: 50rpx;
						height: 50rpx;
					}
				}
			}

			.bottom {
				display: flex;

				.center {
					margin: 0 auto;
				}
			}
		}
	}
</style>
